import * as m from "mithril";
import { Renderer } from "meiosis";
import { meiosisRender, MeiosisRender, RenderIntoElement } from "meiosis-render";

function renderIntoElement(element: Element): Renderer<Mithril.VirtualElement> {
  return function(view: Mithril.VirtualElement): void {
    m.render(element, view);
  };
}

const renderer: MeiosisRender<Mithril.VirtualElement> = meiosisRender(renderIntoElement);

export { renderer };



